#inspectionsContainer {
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    overflow:hidden;
  }
  
  #inspections {
    width: 36em;
    height: 80vh;
    left:2.5%;
    margin-right: auto;
    margin-left: 2.5%;
    border-radius: 50px; /* Rounded corners */
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Soft shadow */
    background-color: #ffffff; /* White background */
    padding: 20px; /* Add padding */
    z-index: 1;
    position: absolute;

  }
  
  #inspections h1 {
    text-align: center;
    font-size: 4em; /* Larger font size */
    color: #222222; /* Dark text color */
  }
  
  #inspections p{
    font-size: 1.4em;
    margin:1em 2em 1em 2em;
    line-height: 1.5em;
  }

  
  #inspectionsContainer img{
    object-fit: cover;
    position: relative;
    top:0;
    left:0;
    z-index: -2;
    filter: blur(4px); /* Apply blur effect */
    width: 100%;
    height:100vh;
    transform: scale(1.1);
    overflow: hidden;
  }