#photogrammetryContainer {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

#photogrammetry {
  width: 35%;
  height: 80vh;
  margin-left: auto;
  margin-right: 2.5%;
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Soft shadow */
  background-color: #ffffff; /* White background */
  padding: 20px; /* Add padding */
}

#photogrammetry h1 {
  text-align: center;
  font-size: 3em; /* Larger font size */
  color: #222222; /* Dark text color */
}

#photogrammetry p {
  padding: 0.5em 1.5em;
  font-size: 1.25em; /* Adjust font size */
  line-height: 1.6; /* Increase line height for better readability */
  color: #333333; /* Darker text color */
  margin-bottom: 20px; /* Add spacing between paragraphs */
}

#gaussianViewerContainer {
  position: absolute;
  padding-right:30%;
  width: 100%;
  height: 100%;
  z-index: -1;
}