
#servicesContainer {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative; /* Ensure relative positioning for children */
}

#services {
  width: 36em;
  height: auto;
  left: 2.5%;
  margin-right: auto;
  margin-left: 2.5%;
  border-radius: 50px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  padding: 20px;
  z-index: 1;
  position: absolute;
  overflow: scroll;
}

#services #about {
  font-size: 1.4em;
  margin: 1em auto;
  padding: 2em auto;
  text-align: center;
  line-height: 1.8em;
  background: #fafafa;
  border-radius: 40px;
}

#services #WIP {
  font-size: 1em;
  padding: 1em auto;
  text-align: left;
  line-height: 1.8em;
  background: #fafafa;
  border-radius: 40px;
}

#services h1 {
  text-align: left;
  font-size: 3em;
  color: #222222;
  margin: 0.5em 0em 0em 1.25em;
}

#services ul {
  margin: 0;
  margin-left: 5%;
  padding: 0 auto;
  list-style: none;
}

#services li {
  text-align: left;
  font-size: 2em;
  color: #555555;
  margin-bottom: 5%;
}

#servicesContainer img {
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  filter: blur(1px);
  width: 100%;
  height: 100vh;
  transform: scale(1.1);
  overflow: hidden;
}

#contact {
  width: 36em;
  height: auto;
  right: 2.5%;
  margin-left: auto;
  margin-right: 2.5%;
  border-radius: 50px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  padding: 20px auto;
  z-index: 1;
  position: absolute;
  overflow: scroll;
}

#contact h1 {
  text-align: left;
  font-size: 3em;
  color: #222222;
  margin: 0.5em 0em 1em 1.25em;
}

#contact ul {
  margin: 0;
  margin-left: 5%;
  padding: 0;
  list-style: none;
}

#contact li {
  text-align: left;
  font-size: 2em;
  color: #555555;
  margin-bottom: 5vh;
}

/* Media Queries for responsiveness */
@media (max-width: 768px) {
  #servicesContainer {
    height: auto;
    flex-direction: column;
    align-items: flex-start;
  }
  
  #services, #contact {
    width: 90%;
    height: auto;
    position: static;
    margin: 1em auto;
    border-radius: 20px;
    padding: 10px;
  }

  #services h1, #contact h1 {
    font-size: 2em;
    margin: 0.5em 0;
  }

  #services li, #contact li {
    font-size: 1.2em;
    margin-bottom: 2vh;
  }

  #services #about, #services #WIP {
    font-size: 1em;
    margin: 1em;
    padding: 0.5em;
  }

  #servicesContainer img {
    height: 100%;
  }
}